	module.exports = {"data":{"markdownRemark":{"html":"<p>Even if you're new to web design and Javascript spooks you, you'd do well to look into some of the techniques that are described in the attached article. </p>\n<p><a title=\"My Favorite Javascripts for Designers: Blakems.com\" href=\"http://www.blakems.com/archives/000087.html\">My Favorite Javascripts for Designers: Blakems.com </a></p>\n<p>Here's a bit of the article: </p>\n<blockquote class=\"large\">\nAre there certain javascripts that you seem to always use from project to project? Lately, I have seen javascript being used more and more effectively on websites. For example, Gmail's user interface and Mike Davidson's sIFR technique. How we use javascript on a website can certainly make a website experience better and more enjoyable. I will never proclaim to be a javascript expert or even write javascript from the scratch. On the other hand, I do excel at the copy, paste, and tweak method.\n</blockquote>","frontmatter":{"created_at":"January 17, 2005","path":"/2005/01/17/javascript-goodies","title":"JavaScript Goodies","tags":["design","javascript"]}}},"pathContext":{}}

